/**
 * @jsx React.DOM
 */
'use strict';

var React = require('react');
var TimeSlider = require('../../src/index');
var GistEmbed = require('react-gist-embed');
var MarkdownFile = require('react-markdown-file');
var ArticleMetadata = require('react-article-metadata');

// https://github.com/facebook/react-devtools
window.React = React;

var Demo = React.createClass({displayName: 'Demo',
  render: function() {
    return (
      React.DOM.div({className: "Demo"}, 
        React.DOM.div({className: "container"}, 
          React.DOM.article({class: "hentry"}, 
            React.DOM.header(null, 
              Masthead(null)
            ), 
            React.DOM.div({className: "row"}, 
              React.DOM.div({className: "col-xs-12 col-md-9 col-md-offset-1"}, 
                React.DOM.div({class: "entry-content"}, 
                  MarkdownFile({fileName: "README.md"})
                )
              )
            )
          )
        )
      )
    );
  }
});

var GithubButton = React.createClass({displayName: 'GithubButton',
  render: function() {
    var iframe = React.DOM.iframe({
      src: "http://ghbtns.com/github-btn.html?user=adamrneary&repo=react-time-slider&type=fork",
      allowtransparency: "true",
      frameborder: "0",
      scrolling: "0",
      width: "53",
      height: "20"
    });
    return iframe;
  }
});

var Masthead = React.createClass({displayName: 'Masthead',
  render: function() {
    return (
      React.DOM.div({className: "Masthead"}, 
        React.DOM.div({className: "row"}, 
          React.DOM.div({className: "col-xs-12 col-md-9 col-md-offset-1"}, 
            React.DOM.h1({className: "entry-title"}, "React Time Slider"), 
            React.DOM.div({className: "entry-summary"}, 
              "A React time slider component using JQuery-UI and Moment"
            ), 
            GithubButton({user: "adamrneary", repo: "react-time-slider"}), 
            React.DOM.div({className: "hidden"}, 
              ArticleMetadata({author: "Adam Neary", dateTimeString: "2014-02-03"})
            )
          )
        ), 
        React.DOM.div({className: "row"}, 
          React.DOM.div({className: "col-xs-12 col-md-3 col-md-offset-1"}, 
            React.DOM.h4(null, "Here is what it looks like:"), 
            Example(null)
          ), 
          React.DOM.div({className: "col-xs-12 col-md-7"}, 
            React.DOM.h4(null, "Here is how you use it:"), 
            GistEmbed({gistId: "a759fd68208808020598"})
          )
        )
      )
    );
  }
});

var Example = React.createClass({displayName: 'Example',
  render: function() {
    var timeSlider = TimeSlider({
      minFrom: 201402,
      maxTo: 201411,
      initialFrom: 201404,
      initialTo: 201408,
      onChange: function(values) {
        console.log(values)
      }
    });
    return timeSlider;
  }
});

React.renderComponent(
  Demo(null),
  document.querySelector('body')
);
